.distributor_selection {
	.upper_heading {
		text-align: center;
		padding: 10px;
		font-size: 30px;
		color: white;
		background: #1989cd;
	}
	.container {
		.upper_heading_number {
			color: white;
			text-align: center;
			.total_distributor {
				background: #1989a1;
				padding: 10px;
				border-radius: 4px;
				width: 40vw;
				margin-right: 5vw;
			}
			.upmapped_distributor {
				background: rgba(179, 39, 39, 0.884);
				padding: 10px;
				border-radius: 4px;
				width: 40vw;
			}
		}
	}
}
